body {
  text-align: left;
  background-color: #fff;
  padding: 0 15px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #51acb6;
  border: 1px solid #51acb6;
  border-radius: 4px;
  padding: 6px 22px;
  font-size: 20px;
}

button[disabled] {
  color: #4d4b4b;
  background-color: #ccc;
  border-color: #ccc;
}

#datetime-picker {
  text-align: center;
  color: #393939;
  border: 1px solid #51acb6;
  border-radius: 4px;
  margin-right: 20px;
  padding: 6px 22px;
  font-size: 20px;
}

#datetime-picker[disabled] {
  color: #4d4b4b;
  background-color: #ccc;
  border-color: #ccc !important;
}

.timer {
  justify-content: left;
  margin-top: 20px;
  display: flex;
}

.field {
  color: #6e2784;
  min-width: 150px;
  border: 2px solid #51acb6;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6px 22px;
  font-size: 20px;
  display: flex;
}

.field:not(:last-child) {
  margin-right: 20px;
}

.value {
  font-size: 30px;
  font-weight: 700;
}

